// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import axios from 'axios';   // AJAX 
import VueAxios from 'vue-axios';// Vue this
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import 'bootstrap';

import VeeValidate from 'vee-validate'   //
// import { Validator } from 'vee-validate'
// import TW from 'vee-validate/dist/locale/zh_TW'    //
import store from './store'  // store  

import App from './App';
import router from './router';
import './Bus';
import currencyFilter from './filters/currency';

import VueAwesomeSwiper from 'vue-awesome-swiper';
Vue.use(VueAwesomeSwiper, /* { default global options } */)

import VueMaterial from 'vue-material';
Vue.use(VueMaterial);

Vue.config.productionTip = false
Vue.use(VueAxios, axios);  // use 

Vue.use(VeeValidate); //API
// Validator.localize('zh-TW', TW)  //

axios.defaults.withCredentials = true; // cookie session

Vue.component('Loading', Loading); //
Vue.filter('currency', currencyFilter); // ('', import )


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store, // store
  components: { App },
  template: '<App/>',
})

//
router.beforeEach((to, from, next) => {
  //console.log('to:', to, 'from:', from, 'next:', next);

  if (to.meta.requiresAuth) {
    //console.log('');
    const api = `${process.env.APIPATH}/api/user/check`; //API
      axios.post(api).then((response) => {  // this.$http
        console.log(response.data);
        if (response.data.success) {
          next(); //
        }else {
          next({
            path: '/login', //
          })
        }
      });
  } else {
    next(); //
  }
  
})



